.styles-button {
  height: 35px;
  width: 190px;
  background: #ee0521;
  color: #fff;
  font-size: 18px;
  display: block;
  margin: 0 auto;
  border: none;
  font-family: 'Manrope', sans-serif;
}

input[type=checkbox] {
  display: none;
}

input[type=checkbox]:checked + label:before {
  color: #000;
}

label:before {
  content: "\2713";
  color: transparent;
  box-sizing: border-box;
  display: inline-block;
  border: 2px solid #000;
  font-size: 13px;
  background: #fff;
  font-weight: 600;
  line-height: 15px;
  margin: -5px 5px 0 0;
  height: 17px;
  width: 17px;
  text-align: center;
  vertical-align: middle;
  transition: color ease .3s;
}

