.select {
  display: inline-block;
  vertical-align: top;
  max-width: 245px;
  padding-right: 100px;
  padding-left: 10px;
}

.select__gap {
  background: transparent;
  color: #ee0521;
  font-size: 18px;
  cursor: pointer;
  position: relative;
}

.select__list {
  background: transparent;
  border: 1px solid #d9d4c9;
}

.select__list.on {
  display: block;
}

.select__item span {
  display: block;
  padding: 10px 15px;
  cursor: pointer;
  color: #333;
}

.select__item.selected,
.select__item span:hover {
  color: #ee0521;
}

